<script>
export default {
  name: 'App',

  data () {
    return {
      font: {
        header: this.isMobile() ? '36' : '48',
        title: this.isMobile() ? '18' : '24',
        note: this.isMobile() ? '12' : '16'
      },
      account: '',
      password: '',
      formHint: null,
      snackbar: false,
      snackbarText: '',
      snackbarTimeout: 6000
    };
  },

  created () {
    this.$vuetify.theme.dark = true;
  },

  methods: {
    verify: function () {
      const validEmailRegex = /^[a-zA-Z0-9.!#$%&'*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$/;

      const emailCheck = !!this.account.match(validEmailRegex);
      const passwordCheck = this.password.length >= 8 && this.password.length <= 16;

      if (emailCheck && passwordCheck) {
        this.formHint = `This is a POSITIVE scenario, as it results in successful sign-up.\n\n【Account】${this.account}（${emailCheck ? '✔' : '✘'}）\n【Password】${this.password}（${passwordCheck ? '✔' : '✘'}）`;
      } else {
        this.formHint = `This is a NEGATIVE scenario, as it results in unsuccessful sign-up.\n\n【Account】${this.account}（${emailCheck ? '✔' : '✘'}）\n【Password】${this.password}（${passwordCheck ? '✔' : '✘'}）`;
      }
    },
    snack: function (text) {
      this.snackbarText = text;
      this.snackbar = true;
    },
    isMobile () {
      switch (this.$vuetify.breakpoint.name) {
        case 'xs':
          return true;
        case 'sm':
          return true;
        case 'md':
          return true;
        case 'lg':
          return false;
        case 'xl':
          return false;
      }
    }
  }
};
</script>

